<template>
  <Secao titulo="programação" id="programacao">
    <div class="row">
      <div class="col-md-3">
        <img
          src="@/assets/imgs/layout/programacao.png"
          alt="Ilustração de pessoa usando celular"
          class="img-fluid"
        />
        <h4>
          <span class="destaque"
            >Anote na agenda, configure o despertador:</span
          >
          uma programação dessas não dá pra perder!
        </h4>
      </div>
      <div class="col-md-8 offset-md-1">
        <h5 class="mb-5">Programação por dia</h5>

        <div
          class="accordion"
          role="tablist"
          v-for="dia in datas"
          :key="dia.id"
        >
          <b-card no-body class="mb-1">
            <b-card-header
              header-tag="header"
              v-b-toggle="`accordion-${dia.dia}`"
              class="p-3 d-flex justify-content-between align-items-center"
              role="tab"
            >
              <div>
                <i class="lni lni-calendar"></i> {{ dia.dia
                }}<span class="laranja">JUN</span> | {{ dia.nome }}
              </div>
              <div><i class="lni lni-chevron-down"></i></div>
            </b-card-header>
            <b-collapse
              visible
              :id="`accordion-${dia.dia}`"
              accordion="my-accordion"
              role="tabpanel"
            >
              <b-card-body>
                <b-list-group>
                  <b-list-group-item
                    v-for="item in filtrarEventosPorDia(dia.dia)"
                    :key="item.id"
                    ><span class="laranja"
                      ><i class="lni lni-alarm-clock"></i>
                      {{ item.horaInicio }} às {{ item.horaFim }}</span
                    >
                    {{ item.titulo }}</b-list-group-item
                  >
                </b-list-group>
              </b-card-body>
            </b-collapse>
          </b-card>
        </div>
      </div>
    </div>
  </Secao>
</template>

<script>
import Secao from "../layout/Secao.vue";
import axios from "axios";

export default {
  name: "App",
  components: {
    Secao,
  },

  data() {
    return {
      programacao: [],
      datas: [
        {
          dia: 1,
          nome: "Quarta-feira",
        },
        {
          dia: 2,
          nome: "Quinta-feira",
        },
        {
          dia: 3,
          nome: "Sexta-feira",
        },
      ],
    };
  },

  created(){
    axios
    .get('https://enati-ifgoiano.github.io/dados/programacao.json')
    .then((response) => {
        this.programacao = response.data;
    })
  },

  methods: {
    filtrarEventosPorDia(dia) {
      return this.programacao.filter((x) => {
        return x.dia == dia ? x : false;
      });
    },
  },
};
</script>

<style lang="scss" scoped>
section#programacao {
  background: var(--begeCor);
  background-image: radial-gradient(
    circle at 25% 50%,
    var(--bege),
    transparent 45%
  );
  .card {
    margin: 10px 0px;
    &:hover {
      cursor: pointer;
    }
  }
}
</style>
