            src="@/assets/imgs/layout/maratona.png"
            class="img-fluid"
            alt="Maratona de programação"
          />
        </div>
        <div class="col-md-7">
          <h4>Como funciona</h4>
          <p>
            Na maratona de programação, os estudantes formam times, que devem tentar resolver
            em 05 horas o maior problema possível dos 10 ou mais problemas que
            são entregues no início da competição. Esses estudantes possuem à
            sua disposição apenas o computador e materiais impressos (livros,
            listas, manuais, etc). A batalha nessa competição é contra o
            relógio. Cada time monta sua estratégia e os programas codificados
            devem ser enviados um por vez.
          </p>
          <p>
            Apesar dos problemas propostos terem um caráter fictício, eles são
            desafiadores e, muitas vezes, servem para resolver soluções do mundo
            real. Grandes empresas acompanham competições de maratona de
            programação e recrutam membros para trabalharem principalmente
            solucionando problemas de otimização. Sendo assim, dedicar a
            resolver desafios propostos em maratonas de programação, também é
            uma forma de se preparar para o mercado de trabalho.
