<script>
import Capa from "@/components/layout/Capa";
import Secao from "@/components/layout/Secao.vue";

import Evento from "@/components/Home/Evento.vue";
import Palestrantes from "@/components/Home/Palestrantes.vue";
import Local from "@/components/Home/Local.vue";
import Programacao from "@/components/Home/Programacao.vue";

export default {
  name: "App",
  components: {
    Capa,
    Evento,
    Secao,
    Palestrantes,
    Programacao,
    Local,
  },

  data() {
    return {
      carregando: true,
      hash: this.$route.hash,
    };
  },

     mounted() {
        this.$nextTick(function () {
            if (this.hash) {
                const refName = this.hash.replace('#', '')
                this.scrollToAnchorPoint(refName)
            }
        })
    },
    methods: {
        scrollToAnchorPoint(refName) {
            const el = this.$refs[refName]
            el.scrollIntoView({ behavior: 'smooth' })
        }
    }
};
</script>

